<template>
	<div >
		<div v-show="expand" style="background-color: white;" class="p-2">
			<label>Give this file a Name</label>
			<span class="esri-icon esri-icon-close close" @click="expand=false"></span>
			<b-input v-model="title"></b-input>
			<b-form-checkbox v-model="uploadForm">Upload form data</b-form-checkbox><br/>
			<b-form-checkbox v-model="uploadResult">Upload result data</b-form-checkbox><br/>
			<b-button @click="upload">Upload</b-button>
		</div>
		<div v-show="!expand" @click="expand = true" class="esri-widget--button esri-widget esri-interactive resize" role="button"  title="Upload to ArcGIS Online">
			<span aria-hidden="true" role="presentation" class="esri-icon esri-icon-applications"></span>
		</div>
	</div>

</template>
<script>
import Papa from 'papaparse'
import * as esriLoad from 'esri-loader'
import {get, set} from 'vuex-pathify'

export default {
	props: {
		portal: Object
	},
	computed: {
		jobId: get('jobId'),
		jobState: get('jobStatus')
	},
	data: function() {
		return {
			expand: false,
			title: "",
			uploadForm: false,
			uploadResult: true
		}
	},
	created: function() {
		console.log(this.portal)
		this.esriRequest = esriLoad.loadModules(["esri/request"]);
	},
	methods: {
		publish: function(response2) {
			this.esriRequest.then(([esriRequest])=>{
				esriRequest(`https://www.arcgis.com/sharing/rest/content/users/${this.portal.user.username}/publish`,{
					method: 'post',
					query: {
						itemID: response2.data.id,
						overwrite: true,
						fileType: 'fileGeodatabase',
						publishParameters: JSON.stringify({name: this.title}),
						f: 'json'
					}
				}).then((response3)=>{
					console.log(response3)
					this.$store.set('jobStatus','success')
				}, (error3)=>{
					console.log(error3);
					this.$store.set('jobStatus', 'failed')
					this.$store.set('messages', error3.details.messages)
					this.$emit('updateStatus', {status: 'failed', messages: error3.details.messages})
				});
			});
		},
		addItem: function(response) {
			console.log('test')
			this.esriRequest.then(([esriRequest])=>{
				esriRequest(`${this.portal.user.userContentUrl}/addItem`,{
					method: 'post',
					query: {
						dataUrl: response.value.url,
						title: this.title,
						overwrite: true,
						type: 'File Geodatabase',
						f: 'json'
					}
				}).then((response2)=>{
					console.log('got here')
					var self = this
					setTimeout(function() {
						self.publish(response2)
					},1000)
				});
			});
		},
		upload: function() {
			this.expand = false;
			this.esriRequest.then(([esriRequest])=>{
				if (this.uploadResult||this.uploadForm) this.$store.set('jobStatus', 'processing')
				if (this.uploadResult) {
					console.log('hi');
					esriLoad.loadModules(["esri/tasks/Geoprocessor"]).then(([Geoprocessor])=>{
						new Geoprocessor(`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem`)
						.getResultData(this.jobId, 'out_route_data',{responseType: 'json'})
						.then((response)=>{
							console.log(response);
							if (!response.value) {
								this.$emit('updateStatus', {status: 'failed', messages: ['Geoprocessing service did not save data']})
								return
							} else {
								var self = this
								setTimeout(function() {
									self.addItem(response)
								},1000);
							}
						});
					});
				}
				/*
				if (this.uploadForm) {
					var fields = ['Orderlocation','Orderx','Ordery','Depotlocation','Depotx','Depoty'];
					for (var type in this.fd) {
						fields = [...fields, ...this.fd[type].data[0].filter((param)=>{
							return param.display
						}).map((param)=>{
							return type + param.param})
						] 
					}
					console.log(fields);
					let maxIter = Math.max(this.fd.Order.count, this.fd.Depot.count, this.fd.Route.count, this.fg.Order.length, this.fg.Depot.length);
					var data = [];
					for (var i = 0; i < maxIter; i++) {
						data.push([
							this.fg.Order[i].location,
							this.fg.Order[i].x,
							this.fg.Order[i].y,
							this.fg.Depot[i].location,
							this.fg.Depot[i].x,
							this.fg.Depot[i].y,
							...this.fd.Order.data[i].filter((param)=>{
								return param.display
							}).map((param)=>{
								return param.value
							}),
							...this.fd.Depot.data[i].filter((param)=>{
								return param.display
							}).map((param)=>{
								return param.value
							}),
							...this.fd.Route.data[i].filter((param)=>{
								return param.display
							}).map((param)=>{
								return param.value
							})
						]);
					}
					var csv = Papa.unparse({
						fields: fields,
						data: data
					});	

					esriRequest(`${this.portal.user.userContentUrl}/addItem`,{
						method: 'post',
						query: {
							title: `${this.title}_form`,
							overwrite: true,
							type: 'CSV',
							text: `data:text/plain;charset=utf-8,${encodeURIComponent(csv)}`,
							fileName: `${this.title}_form.csv`,
							f: 'json'
						}
					}).then((success)=>{
						console.log(success);
						this.$emit('updateStatus', {status: 'success'})
					}, (error)=>{
						console.log(error);
						this.$emit('updateStatus', {status: 'failed', messages: error.details.messages})
					})
				}
				*/
			});
		}
	}
}
</script>
<style>
.resize {
	transition: all 0.5s ease;
}

.close{
	position: relative;
	top: 4px;
	right: 4px;
	font-size: 16px;
}
</style>